const cards = [
    {id:1, text: "    ", price: "550 ", weight: "200 ", description:"     ,  !      ,     .  ?", time:"10-25 " },
    {id:2, text: "  ", price: "700 ", weight: "310", description:" cheese,   !    ,       .         .  ,    .", time:"10-25 " },
    {id:3, text: "   ", price: "650", weight: "310 ", description:"  ,   .        .       ,          .", time:"10-25 " },
    {id:4, text: "    /BBQ/", price: "750 ", weight: "150 ", description:"  ,    ? BBQ ?,   !  ,    ,   .", time:"10-25 " },
    {id:5, text: "  ", price: "900 ", weight: "300" , description:", ?       !     , ,     .         .", time:"10-25 "},
    {id:6, text: "  &   ", price: "250 ", weight: "250 ", description:"    ? ,      ,  ,   ,    ", time:"10-25 " },
    {id:7, text: "  &   ", price: "300 ",  weight: "250 ", description:"   ,   ,  !  , ,  .  ,   , !", time:"10-25 " },
    {id:8, text: "  &   ", price: "300 ",  weight: "250 " , description:" ?   , !      ,      .", time:"10-25 "},
    {id:9, text: "  &   ", price: "270 ",  weight: "250 " , description:"     ,  .     .    !", time:"10-25 "},
    {id:10, text: "  BBQ/ //", price: "400 ",  weight: "260 ", description:"   !    ,    BBQ,  ,     .      !", time:"10-25 " },
    {id:11, text: "  ", price: "250 ",  weight: "250 " , description:"  ,   ! ,      .  ,         .", time:"10-25 "},
  
  ];



const RecomHot = () => {
  const navigate = useNavigate();

  const handleCardClick = (card) => {
    navigate('/barhot', { state: { dish: card, fromRecomendations: true } });
  };

  return (
    <div className={s.drinks}>
        <p className={s.rec}> </p>
      <div className={s.cardsContainer}>
        
        {cards.map((card, index) => (
          <div key={index} onClick={() => handleCardClick(card)}>
            <CardPrice 
              text={card.text} 
              price={card.price} 
              weight={card.weight} 
            />
          </div>
        ))}
      </div>
      <CartButton />
    </div>
  );
};

export default RecomHot;