import React, { useState } from 'react';
import TopBar from '../../Complite/TopBar/TopBar';
import s from './RegRest.module.css';
import { useNavigate, useLocation } from 'react-router-dom';
import { v4 as uuidv4 } from 'uuid';

const RegRest = () => {
  const location = useLocation();
  const { state } = location;
  const totalPrice = state?.totalPrice || 0;  //   totalPrice
  const time = state?.time || '';
  const cartItems = state?.cartItems || []; //   



  const navigate = useNavigate();
  const [phoneNumber, setPhoneNumber] = useState('');
  const [guestCount, setGuestCount] = useState('');
  const [comment, setComment] = useState('');

  const handlePayment = async () => {
    const idempotenceKey = uuidv4(); //   Idempotence Key
    const orderId = uuidv4(); //    

    try {
      const requestData = {
        orderId,
        totalPrice: Number(totalPrice), // ,   
        orderType: 'delivery',
        comment: comment || '  ',
        phoneNumber,
        guestCount: Number(guestCount), //   
        orderTime: time,
              cartItems:[
                {text:item.text, count:item.count, price:item.price} 
              ]

      };

      const response = await fetch('https://sunvillrest.netlify.app/.netlify/functions/payment', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Idempotence-Key': idempotenceKey, //   
        },
        body: JSON.stringify(requestData), //    
        mode: 'cors',
      });

      const data = await response.json();

      if (response.ok) {
        const confirmationUrl = data.confirmationUrl;
        window.location.href = confirmationUrl; //     
      } else {
        alert(`: ${data.message}`);
      }
    } catch (error) {
      alert('    .  .');
    }
  };

  const handleClick = (path) => {
    navigate(path, { state: { time, totalPrice } });
  };

  return (
    <div className={s.rest}>
      <TopBar text="" />
        {/*    */}
    <div className={s.cartItems}>
      <h3> :</h3>
      {cartItems.map((item, index) => (
        <div key={index} className={s.cartItem}>
          <p>{item.text}  {item.count} .</p>
        </div>
      ))}
    </div>
      <div className={s.restform}>
        <input
          onClick={() => handleClick('/time')}
          className={s.input}
          type="text"
          placeholder=' '
          value={time || ''} //   
          readOnly
        />
        <input
          className={s.input}
          type="number"
          placeholder='  '
          value={guestCount}
          onChange={(e) => setGuestCount(e.target.value)}
        />
        <input
          className={s.input}
          type="tel"
          placeholder='   '
          value={phoneNumber}
          onChange={(e) => setPhoneNumber(e.target.value)}
        />
        <input
          className={s.input}
          type="text"
          placeholder='  '
          value={comment}
          onChange={(e) => setComment(e.target.value)}
        />
      </div>
      <div className={s.option}>
        <p></p>
        <input type="checkbox" id="checkbox" />
      </div>
      <div className={s.price}>
        <p> </p>
        <p>{totalPrice || 0} </p>
      </div>
      <div className={s.result}>
        <p>{totalPrice || 0} </p>
        <button className={s.pay} onClick={handlePayment}></button>
      </div>
    </div>
  );
};

export default RegRest;
