import React from 'react';
import TopBar from '../../Complite/TopBar/TopBar';
import s from './RegDel.module.css';
import { useNavigate, useLocation } from 'react-router-dom';
import { useDeliveryContext } from '../../Contextes/RegContext';

const RegDel = () => {
  const { deliveryData } = useDeliveryContext();
  const navigate = useNavigate();
  const location = useLocation();
  const { totalPrice } = location.state || {};

  const { state } = useLocation();
  const { time } = state;

  const handleClick = (path) => {
    navigate(path, { state: { time, totalPrice } });

  };

  return (
    <div className={s.del}>
      <TopBar text="" />
      <div className={s.delform}>
        <input
          onClick={() => handleClick('/timedel')}
          className={s.input}
          type="text"
          placeholder=" "
          value={deliveryData.time || ''}
          readOnly
        />
        <input
          onClick={() => handleClick('/address')}
          className={s.input}
          type="text"
          placeholder="  "
          value={deliveryData.flat || ''}
          readOnly
        />
        <input className={s.input} type="number" placeholder="  " />
        <input className={s.input} type="tel" placeholder="   " />
        <input className={s.input} type="text" placeholder="  " />
      </div>
      <div className={s.option}>
        <p></p>
        <input type="checkbox" id="checkbox" />
      </div>
      <div className={s.price}>
        <p> </p>
        <p>{totalPrice || 0} </p> {/*    */}
      </div>
      <div className={s.result}>
        <p>{totalPrice || 0} </p> {/*    */}
        <button className={s.pay}></button>
      </div>
    </div>
  );
};

export default RegDel;
